body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.question-number {
  opacity: 0.6;
  font-weight: bold;
  color: #6a5be2;
}

.question-title {
  padding-top: 12px;
  padding-bottom: 12px;
}

.content-container {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #6a5be2;
  font-size: 16px;
  min-height: 100vh;
}

.content {
  padding: 24px;
  width: 600px;
  box-sizing: border-box;
  border-radius: 8px;
  box-shadow: 0px 24px 48px 0 rgba(0, 0, 0, 0.16);
  background-color: white;
}

.answer {
  padding: 12px;
  margin-top: 12px;
  margin-bottom: 12px;
  box-sizing: border-box;
  border-radius: 8px;
  border: 2px solid #6a5be2;
  cursor: pointer;
}

.question {
  display: flex;
  flex-direction: column;
}

@media (hover: hover) {
  .answer:hover {
    background-color: #6a5be2;
    opacity: 0.6;
    color: white;
  }
}
.selected {
  background-color: #6a5be2 !important;
  color: white !important;
}

@media (hover: hover) {
  .submit:hover {
    background-color: #6a5be2;
    opacity: 0.6;
    color: white;
  }
}

.submit {
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #6a5be2;
  width: 80px;
  height: 40px;
  border-radius: 12px;
  cursor: pointer;
  margin-right: 12px;
}

@media only screen and (max-width: 600px) {
  .content {
    width: 100vw;
    border-radius: 0px;
  }
}

.finish {
  color: #6a5be2;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  border-radius: 12px;
  border: 2px solid #6a5be2;
  box-sizing: border-box;
  width: 80px;
  height: 40px;
  cursor: pointer;
}

.next {
  color: #6a5be2;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  border-radius: 12px;
  border: 2px solid #6a5be2;
  box-sizing: border-box;
  width: 80px;
  height: 40px;
  cursor: pointer;
}

@media (hover: hover) {
  .next:hover {
    opacity: 0.6;
  }
}

.buttons-container {
  display: flex;
  flex-direction: row;
  margin-bottom: 12px;
}

.feedback {
  box-sizing: border-box;
  padding: 12px;
  border-radius: 4px;
  line-height: 1.6;
  overflow-wrap: break-word;
}

.finished-container {
  font-size: 32px;
  line-height: 1.6;
  display: flex;
  justify-content: center;
  height: 550px;
  align-items: center;
  flex-direction: column;
}

.correct-answer {
  border: 2px solid green;
}

.correct {
  background-color: #9bf599;
}

.incorrect {
  background-color: #ff8f8f;
}
